
function add_listener(elementId,streams,downloadElement){
    
    document.addEventListener('change',function(e){
        if(e.target && e.target.id== elementId){
            let res = streams.find(obj => obj.itag == e.target.value);
            downloadElement.href=res.download_link;
            downloadElement.setAttribute("download",res.filename);
        }
     })
}
function showResult(data,el){
            info=data.info;
            streams=data.streams;
            output=`
            <header class="modal__header">
                <h2 class="modal__title" id="modal-1-title">
                  ${info.title}
                </h2>
              </header>
            `;
            output+=`

            <main class="modal__content" id="modal-1-content">
                <div class="video-thumbnail">
                    <img src="${info.thumbnail_url}" alt="${info.title}" />
                </div>
                    <div id="quality-selection">
                        <label>کیفیت ها</label>
                        <select name="quality" id="quality">
            `;

            for(let i=0;i<streams.length;i++){
                output+=`
                    <option value="${streams[i].itag}">کیفیت : ${streams[i].resolution} &nbsp;&nbsp; اندازه فایل : ${streams[i].filesize} &nbsp;&nbsp; نوع فایل : ${streams[i].type}</option>
                `;
            }
            output+="</select><a id='download' class='button'>دانلود</a></div></div></main>";
            el.innerHTML=output;
            loader.classList.remove("show");
            MicroModal.show('info-modal');
        }
        //modal initialization

        let downloadBtn = document.querySelector("#download-btn");
        downloadBtn.addEventListener("click", function (e) {
            let loader=document.querySelector("#loader");
            loader.classList.add("show");
            let url = document.querySelector("input#url").value;
              axios({
                  method: 'get',
                  url: 'http://coderguy.pythonanywhere.com/youtube/streams?url='+url,
             
                })
                .then(res=>{
                   showResult(res.data,document.querySelector("#result"));
                   add_listener("quality",res.data.streams,document.querySelector("#download"));
                });
            
        });